.app-container {
  text-align: center;
  font-family: Arial, sans-serif;
}

.login-container,
.polls-container,
.results-container {
  margin: 50px auto;
  padding: 20px;
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
}

.category {
  margin-bottom: 20px;
}

input {
  width: 100%;
  padding: 8px;
  margin: 10px 0;
}

button {
  padding: 10px;
  margin: 5px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

button:hover:not(:disabled) {
  background-color: #0056b3;
}
.logout-btn {
  margin-top: 20px;
  padding: 10px;
  background-color: #ff4d4d;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.logout-btn:hover {
  background-color: #cc0000;
}

.summary-btn, .back-btn {
  margin-top: 10px;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.summary-btn:hover, .back-btn:hover {
  background-color: #45a049;
}

